@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
#display-screen {
    min-height: 20px;
    font-family: digital;
    font-size: 20px;
    text-align: right;
    vertical-align: text-top;
    line-height: 20px;
    overflow-wrap: break-word;
    word-wrap: break-word
}

.operation-screen {
    min-height: 40px;
    margin: 0;
    color: orange;
}

#display {
    color: white;
}
.num-continer {
    display: grid;
    grid-gap: 2.5px;
    grid-template-areas: 
    "one two three"
    "four five six"
    "seven eight nine"
    "zero zero zero";
}

.num-button {
    background-color: #4d4d4d;
    border: none;
    color: #fff;
    text-align: center;
}

.num-button:hover {
    background-color: #5c5b5b;
}



#zero {
    grid-area: zero;
}

#one {
    grid-area: one;
}

#two {
    grid-area: two;
}

#three {
    grid-area: three;
}

#four {
    grid-area: four;
}

#five {
    grid-area: five;
}

#six {
    grid-area: six;
}

#seven {
    grid-area: seven;
}

#eight {
    grid-area: eight;
}

#nine {
    grid-area: nine;
}
.prim-continer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-gap: 2.5px;
    margin: 2.5px;
}

.button {
    background-color: #4d4d4d;
    border: none;
    color: #fff;
    text-align: center;
}

.button:hover {
    background-color: #5c5b5b;
}
.sec-continer {
    display: grid;
    grid-gap: 2.5px;
    grid-template-areas: 
    "clear clear"
    "equals decimal";
    margin: 2.5px;
}

.button {
    background-color: #4d4d4d;
    border: none;
    color: #fff;
    text-align: center;
}

.button:hover {
    background-color: #5c5b5b;
}

#clear {
    background-color: #39ac39;
    grid-area: clear;
}

#equals {
    background-color: #004466;
    grid-area: equals;
}

#decimal {
    grid-area: decimal;
}
* {
    font-family: 'Orbitron', sans-serif;
}
body {
    height: 90vh;
    background-color: #c2c2d6;
    display: flex;
    align-items: center;
    justify-content: center;
}

.calculator-continer{
    position: relative;
    background-color: black;
    min-width: 350px;
    display: grid;
    grid-template-rows: 2fr 4fr;
    border: thick double #ac3939;
    border-radius: 2.5%;
    box-shadow: 3px 3px #7f7f88, -1em 0 .4em #242425;
}

.button-continer {
    display: grid;
    grid-template-rows: 3fr 1fr;
}

.operations-continer {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.linked {
    text-align: center;
    font-size: small;
}

.linked a {
    text-decoration: none;
    color: rgb(17, 233, 222);
}
